/* Styles go here */
* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
    width: 12px;
	background:#fff !important;
}
::-webkit-scrollbar-thumb {
    background: #3cb371;
	border:0;
}
::-webkit-scroll-thumb:hover {background:#3cb371}
::-webkit-scrollbar-track {
	background:#fff;
}

#page-wrap {
  padding-bottom: 10px;
}
.color-nav {background-color: #3cb371; }
#account {color:#fff; font-size:11px; }
#content-placeholder {padding:80px 20px 20px 20px;}
h1 {font-size:26px;font-family: 'Roboto', sans-serif; font-weight:bold;}
h2 {font-size:23px;font-family: 'Roboto', sans-serif; font-weight:bold; margin-top:60px;}
h3 {font-size:20px;font-family: 'Roboto', sans-serif; font-weight:bold;}
p, body {font-family: 'Roboto', sans-serif; font-size:16px; line-height:28px;}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 30px;
    height: 25px;
    right: 13px;
    top: 12px;
}
/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}
ul.navbar-nav.px-3 {
    margin-right: 40px;
}
.bm-burger-button span {
    background: #fff;
}
/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
  top:0;
}

/* General sidebar styles */
.bm-menu {
  background: #fff;
  color:#333;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}
.bm-menu a {color:#3cb371; margin-bottom:5px;}
.bm-menu a:hover {color:#3cb371; text-decoration: none;}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

